import dynamic from 'next/dynamic';
import Link from 'next/link';
const SubtitleComponent = dynamic(() => import('@/components/common/SubtitleComponent'), { ssr: false });
const HighLightArticleComponent = dynamic(() => import('@/components/common/HighLightArticleComponent'));

interface ArticleSectionProps {
    api?: string | null;
    title: string;
}
export default function HightLightArticleSection({ props }: { props: ArticleSectionProps }) {
    return (
        <div className="bg-[#F2EEE6] py-20 flex flex-row justify-center">
            <div className="lg:w-[1200px] md:w-[864px] sm:w-[608px] xs:w-[280px] lg:px-20 md:px-0 sm:px-0">
                <div className="flex flex-col justify-between">
                    <SubtitleComponent subTitle={props.title} className='text-primary' />
                    <HighLightArticleComponent props={{ loadingItems: 3, title: props.title }} />
                    <Link href={{
                        pathname: props.title.toLowerCase() == 'ulasan media' ? '/media' : '/articles',
                    }} className={`border-2 border-[#707070] text-center mt-6 py-6 text-partial font-dm-sans font-bold lg:text-xl text-base`}>Selengkapnya</Link>
                </div>
            </div>
        </div>
    )
}
